import React, { useEffect, useState, useCallback } from 'react';
import { QRCodeSVG } from 'qrcode.react'; 
import PropTypes from 'prop-types';
import './PaymentModal.css';

const PaymentModal = ({ isOpen, qrcodeUrl, onClose, expiresAt, price = 0.99 }) => {
  // ä½¿ç”¨useCallbackåŒ…è£…å‡½æ•°ï¼Œé˜²æ­¢å®ƒåœ¨æ¯æ¬¡æ¸²æŸ“æ—¶éƒ½é‡æ–°åˆ›å»º
  const calculateInitialCountdown = useCallback(() => {
    if (expiresAt) {
      const remainingTime = Math.max(0, Math.floor((expiresAt - Date.now()) / 1000));
      return remainingTime;
    }
    return 300; // é»˜è®¤5åˆ†é’Ÿ
  }, [expiresAt]);
  
  const [countdown, setCountdown] = useState(calculateInitialCountdown());
  
  // å½“æ¨¡æ€æ¡†æ‰“å¼€æˆ–expiresAtå˜åŒ–æ—¶é‡æ–°è®¡ç®—å€’è®¡æ—¶
  useEffect(() => {
    if (isOpen) {
      setCountdown(calculateInitialCountdown());
    }
  }, [isOpen, calculateInitialCountdown]);

  // å€’è®¡æ—¶æ•ˆæœ
  useEffect(() => {
    if (!isOpen) return;

    const timer = setInterval(() => {
      setCountdown(prev => {
        if (prev <= 0) {
          clearInterval(timer);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, [isOpen]);

  // æ ¼å¼åŒ–æ—¶é—´æ˜¾ç¤º
  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  if (!isOpen) return null;

  return (
    <div className="payment-modal">
      <div className="modal-content">
        <div className="modal-header">
          <h3>æ‰«ç æ”¯ä»˜ Â¥{price}</h3>
          <button className="close-button" onClick={onClose}>Ã—</button>
        </div>
        
        <div className="qrcode-wrapper">
          {qrcodeUrl ? (
            <>
              <QRCodeSVG 
                value={qrcodeUrl} 
                size={200}
                fgColor={countdown <= 0 ? "#cccccc" : "#2d3748"}
                level="H"
              />
              <p className="countdown" style={{color: countdown <= 0 ? "#ff0000" : "inherit"}}>
                {countdown <= 0 ? "äºŒç»´ç å·²å¤±æ•ˆ" : `å‰©ä½™æ—¶é—´: ${formatTime(countdown)}`}
              </p>
              {countdown <= 0 && (
                <div className="expired-notice">
                  <p>äºŒç»´ç å·²è¿‡æœŸï¼Œè¯·å…³é—­åé‡è¯•</p>
                  <button onClick={onClose} className="retry-button">å…³é—­é‡è¯•</button>
                </div>
              )}
            </>
          ) : (
            <div className="loading">æ­£åœ¨ç”Ÿæˆæ”¯ä»˜ç ...</div>
          )}
        </div>

        <div className="modal-footer">
          <p>è¯·ä¿å­˜äºŒç»´ç ï¼Œä½¿ç”¨æ”¯ä»˜å®æ‰«æå®Œæˆæ”¯ä»˜</p>
          <p className="no-refund-policy">âš ï¸ <strong>ä»˜æ¬¾é¡»çŸ¥</strong>ï¼šæœ¬æœåŠ¡ä¸ºè™šæ‹Ÿå†…å®¹ï¼Œæ”¯ä»˜åç«‹å³æä¾›ï¼Œå› æ­¤ä»˜æ¬¾åä¸äºˆé€€æ¬¾ã€‚è¯·ç¡®è®¤åå†ä»˜æ¬¾ã€‚</p>
        </div>
      </div>
    </div>
  );
};

PaymentModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  qrcodeUrl: PropTypes.string,
  onClose: PropTypes.func.isRequired,
  expiresAt: PropTypes.number,
  price: PropTypes.number
};

export default PaymentModal;
