import React, { useState, useEffect, useMemo, useCallback } from 'react';
import Form from './components/Form';
import PaymentModal from './components/PaymentModal';
import AnalysisSection from './components/AnalysisSection';
import './App.css';
import { createOrderStream, initiatePayment } from './api';

const ANALYSIS_TYPES = [
  { id: 'health', name: 'å¥åº·åˆ†æ' },
  { id: 'career', name: 'äº‹ä¸šåˆ†æ' },
  { id: 'wealth', name: 'è´¢è¿åˆ†æ' },
  { id: 'marriage', name: 'å©šå§»åˆ†æ' }
];

function App() {
  const [analysisData, setAnalysisData] = useState({
    currentOrderId: null,
    preview: '',
    unlocked: {},
    contents: {
      health: null,
      career: null,
      wealth: null,
      marriage: null
    },
    analysisOrderIds: {
      health: null,
      career: null,
      wealth: null,
      marriage: null
    },
    paymentInfo: {
      health: null,
      career: null,
      wealth: null,
      marriage: null
    },
    generating: {
      health: false,
      career: false,
      wealth: false,
      marriage: false
    }
  });
  const [paymentState, setPaymentState] = useState({
    show: false,
    qrcodeUrl: '',
    processingType: null,
    expiresAt: null,
    price: 0.99
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [connections, setConnections] = useState({
    main: null,
    health: null,
    career: null,
    wealth: null,
    marriage: null
  });
  const [progress, setProgress] = useState(0);
  const [resetLoadingTypeCallback, setResetLoadingTypeCallback] = useState(null);

  const closeConnection = (type) => {
    if (connections[type]) {
      connections[type].close();
      setConnections(prev => ({
        ...prev,
        [type]: null
      }));
    }
  };

  const closeAllConnections = useCallback(() => {
    Object.keys(connections).forEach(type => {
      if (connections[type]) {
        connections[type].close();
      }
    });
    setConnections({
      main: null,
      health: null,
      career: null,
      wealth: null,
      marriage: null
    });
  }, [connections]);

  const storage = useMemo(() => ({
    save: (data) => {
      if (typeof data !== 'object' || data === null) {
        setError('æ— æ³•ä¿å­˜æ— æ•ˆçš„åˆ†ææ•°æ®');
        return false;
      }
  
      try {
        localStorage.setItem('analysisData', JSON.stringify(data));
        return true;
      } catch (error) {
        setError('ä¿å­˜æ•°æ®å¤±è´¥ï¼Œè¯·æ£€æŸ¥æµè§ˆå™¨å­˜å‚¨ç©ºé—´');
        return false;
      }
    },
  
    load: () => {
      try {
        const data = localStorage.getItem('analysisData');
        if (!data) return null;
  
        const parsedData = JSON.parse(data);
  
        if (
          typeof parsedData === 'object' &&
          parsedData !== null &&
          typeof parsedData.preview === 'string' &&
          typeof parsedData.unlocked === 'object' &&
          typeof parsedData.contents === 'object' &&
          typeof parsedData.currentOrderId === 'string' &&
          typeof parsedData.analysisOrderIds === 'object'
        ) {
          if (!parsedData.paymentInfo) {
            parsedData.paymentInfo = { health: null, career: null, wealth: null, marriage: null };
          }
          if (!parsedData.generating) {
            parsedData.generating = { health: false, career: false, wealth: false, marriage: false };
          }
          return parsedData;
        }
        
        throw new Error('æ•°æ®æ ¼å¼ä¸æ­£ç¡®');
      } catch (error) {
        localStorage.removeItem('analysisData');
        return null;
      }
    },

    savePaymentInfo: (type, paymentInfo) => {
      try {
        const key = `payment_${type}`;
        if (paymentInfo === null) {
          localStorage.removeItem(key);
        } else {
          localStorage.setItem(key, JSON.stringify(paymentInfo));
        }
        return true;
      } catch (error) {
        console.error('ä¿å­˜æ”¯ä»˜ä¿¡æ¯å¤±è´¥', error);
        return false;
      }
    },

    loadPaymentInfo: (type) => {
      try {
        const key = `payment_${type}`;
        const data = localStorage.getItem(key);
        if (!data) return null;
        
        return JSON.parse(data);
      } catch (error) {
        localStorage.removeItem(`payment_${type}`);
        return null;
      }
    }
  }), [setError]);

  const initialAnalysisState = {
    currentOrderId: null,
    preview: '',
    unlocked: {},
    contents: { health: '', career: '', wealth: '', marriage: '' },
    analysisOrderIds: { health: null, career: null, wealth: null, marriage: null },
    paymentInfo: { health: null, career: null, wealth: null, marriage: null },
    generating: { health: false, career: false, wealth: false, marriage: false }
  };

  const clearAnalysisData = () => {
    try {
      localStorage.removeItem('analysisData');
      ANALYSIS_TYPES.forEach(type => {
        storage.savePaymentInfo(type.id, null);
      });
      setAnalysisData(initialAnalysisState);
      setPaymentState({ show: false, qrcodeUrl: '', processingType: null, expiresAt: null, price: 0.99 });
      setLoading(false);
      setError('');
      setProgress(0);
      closeAllConnections();
    } catch (error) {
      setError('æ¸…é™¤æ•°æ®å¤±è´¥');
    }
  };
  
  const handleSubmit = async (eightCharacters) => {
    setLoading(true);
    setError('');
    setAnalysisData(initialAnalysisState);
    setProgress(0);

    closeAllConnections();

    const progressInterval = setInterval(() => {
      setProgress(prev => {
        if (prev >= 95) {
          clearInterval(progressInterval);
          return 95;
        }
        return prev + 1;
      });
    }, 200);

    const newES = createOrderStream(eightCharacters, {
      onStream: (chunk) => {
        setAnalysisData(prev => ({
          ...prev,
          preview: prev.preview + chunk
        }));
        setLoading(false);
        setProgress(100);
        clearInterval(progressInterval);
      },
      onComplete: (prevOrderId) => {
        setAnalysisData(prev => ({
          ...prev,
          currentOrderId: prevOrderId
        }));
      },
      onError: (message) => {
        setError(message || 'ç”Ÿæˆé¢„è§ˆå¤±è´¥');
        setLoading(false);
        clearInterval(progressInterval);
      }
    });
    
    setConnections(prev => ({
      ...prev,
      main: newES
    }));
  };

  const unlockAnalysis = (type) => {
    // å¦‚æœå·²ç»è§£é”è¯¥ç±»å‹ï¼Œåˆ™ç›´æ¥è¿”å›
    if (analysisData.unlocked[type]) return Promise.resolve();
    
    if (!analysisData.currentOrderId) return Promise.resolve();

    const now = Date.now();
    const existingPayment = storage.loadPaymentInfo(type);
    
    closeConnection(type);
    
    if (existingPayment && existingPayment.expiresAt > now) {
      setAnalysisData(prev => ({
        ...prev,
        paymentInfo: {
          ...prev.paymentInfo,
          [type]: existingPayment
        },
        analysisOrderIds: {
          ...prev.analysisOrderIds,
          [type]: existingPayment.orderId
        }
      }));
      
      setPaymentState({
        show: true,
        qrcodeUrl: existingPayment.qrcodeUrl,
        processingType: type,
        expiresAt: existingPayment.expiresAt,
        price: existingPayment.price
      });
      
      setupPaymentConnection(type, existingPayment.orderId);
      return Promise.resolve();
    }

    const initialPaymentState = {
      show: true,
      qrcodeUrl: '',
      processingType: type,
      expiresAt: null,
      price: 0.99
    };
    setPaymentState(initialPaymentState);

    setupPaymentConnection(type);
    return Promise.resolve();
  };

  const setupPaymentConnection = (type, existingOrderId = null) => {
    const paymentES = initiatePayment(
      analysisData.currentOrderId,
      type,
      {
        onPaymentQR: (qrcodeUrl, price) => {
          const expiresAt = Date.now() + 5 * 60 * 1000;
          
          const paymentInfo = {
            qrcodeUrl,
            orderId: price,
            expiresAt,
            createdAt: Date.now(),
            price: price
          };
          
          setPaymentState(prev => ({ 
            ...prev, 
            qrcodeUrl,
            expiresAt,
            price 
          }));
          
          setAnalysisData(prev => ({
            ...prev,
            analysisOrderIds: {
              ...prev.analysisOrderIds,
              [type]: price
            },
            paymentInfo: {
              ...prev.paymentInfo,
              [type]: paymentInfo
            }
          }));
          
          storage.savePaymentInfo(type, paymentInfo);
        },
        onAlreadyPaid: (content) => {
          setAnalysisData(prev => ({
            ...prev,
            unlocked: { ...prev.unlocked, [type]: true },
            contents: { ...prev.contents, [type]: content },
            paymentInfo: { ...prev.paymentInfo, [type]: null },
            generating: { ...prev.generating, [type]: false }
          }));
          setPaymentState({ show: false, qrcodeUrl: '', processingType: null, expiresAt: null, price: 0.99 });
          storage.savePaymentInfo(type, null);
          closeConnection(type);
        },
        onTryAgain: () => {
          setError(`${type}åˆ†ææ­£åœ¨å¤„ç†ä¸­ï¼Œè¯·ç¨åå†è¯•`);
          setPaymentState({ show: false, qrcodeUrl: '', processingType: null, expiresAt: null, price: 0.99 });
          closeConnection(type);
        },
        onAnalysisChunk: (chunk) => {
          setAnalysisData(prev => ({
            ...prev,
            contents: {
              ...prev.contents,
              [type]: (prev.contents[type] || '') + chunk
            }
          }));
        },
        onPaymentstatus: () => {
          setAnalysisData(prev => ({
            ...prev,
            unlocked: { ...prev.unlocked, [type]: true },
            paymentInfo: { ...prev.paymentInfo, [type]: null },
            generating: { ...prev.generating, [type]: true }
          }));
          setPaymentState({ show: false, qrcodeUrl: '', processingType: null, expiresAt: null, price: 0.99 });
          storage.savePaymentInfo(type, null);
        },
        onAnalysisEnd: () => {
          setAnalysisData(prev => ({
            ...prev,
            generating: { ...prev.generating, [type]: false }
          }));
          closeConnection(type);
        },
        onError: (message) => {
          setError(message || 'æ”¯ä»˜æµç¨‹å‡ºé”™');
          setPaymentState({ show: false, qrcodeUrl: '', processingType: null, expiresAt: null, price: 0.99 });
          closeConnection(type);
        }
      },
      existingOrderId
    );

    setConnections(prev => ({
      ...prev,
      [type]: paymentES
    }));
  };

  const handleResetLoadingType = callback => {
    setResetLoadingTypeCallback(() => callback);
  };

  const handleClosePaymentModal = () => {
    if (paymentState.processingType) {
      closeConnection(paymentState.processingType);
    }
    
    setPaymentState({ show: false, qrcodeUrl: '', processingType: null, expiresAt: null, price: 0.99 });
    if (resetLoadingTypeCallback) {
      resetLoadingTypeCallback();
    }
  };

  useEffect(() => {
    if (analysisData.preview) {
      storage.save(analysisData);
    }
  }, [analysisData, storage]);

  useEffect(() => {
    const loadedData = storage.load();
    if (loadedData) {
      setAnalysisData(loadedData);
    }
    return () => {
      closeAllConnections();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [storage]);

  useEffect(() => {
    ANALYSIS_TYPES.forEach(type => {
      const paymentInfo = storage.loadPaymentInfo(type.id);
      if (paymentInfo) {
        if (paymentInfo.expiresAt > Date.now()) {
          setAnalysisData(prev => ({
            ...prev,
            paymentInfo: {
              ...prev.paymentInfo,
              [type.id]: paymentInfo
            },
            analysisOrderIds: {
              ...prev.analysisOrderIds,
              [type.id]: paymentInfo.orderId
            }
          }));
        } else {
          storage.savePaymentInfo(type.id, null);
        }
      }
    });
  }, [storage]);

  return (
    <div className="App">
      <header className="header">
        <div className="header-content">
          <div className="branding">
            <h1>MingLiç†å‘½</h1>
            <p className="slogan">ä¸€é˜´ä¸€é˜³ä¹‹è°“é“</p>
          </div>
          <div className="logo-container">
            <img src="/logo.png" alt="Logo" className="logo" />
          </div>
        </div>
      </header>

        {error && <div className="error-message">{error}</div>}
        
        {loading ? (
            <div className="loading-wrapper">
            <div className="loading">
              <div className="loading-text">
                â³ æ·±åº¦åˆ†æé€šå¸¸éœ€è¦ 30 ç§’
                <div className="sub-hint">ï¼ˆæœŸé—´è¯·ä¸è¦åˆ·æ–°é¡µé¢ï¼‰</div>
              </div>
              <div className="progress-container">
                <div 
                  className="progress-bar" 
                  style={{ width: `${progress}%` }}
                ></div>
              </div>
              <div className="progress-percentage">{progress}%</div>
            </div>
          </div>
        ) : (
          !analysisData.preview ? (
            <Form onSubmit={handleSubmit} />
          ) : (
            <AnalysisSection
              preview={analysisData.preview}
              unlocked={analysisData.unlocked}
              contents={analysisData.contents}
              onUnlock={unlockAnalysis}
              analysisTypes={ANALYSIS_TYPES}
              onGoBack={clearAnalysisData}
              onComplete={analysisData.currentOrderId}
              generating={analysisData.generating}
              onResetLoadingType={handleResetLoadingType}
            />
          )
        )}

        <PaymentModal
          isOpen={paymentState.show}
          qrcodeUrl={paymentState?.qrcodeUrl}
          onClose={handleClosePaymentModal}
          expiresAt={paymentState.expiresAt}
          price={paymentState.price}
        />
    
      <footer>
        <div className="sfooterContent">
          <p className="sdisclaimer">
            æ‹’ç»å‘æœªæˆå¹´äººæä¾›æœåŠ¡ï¼Œä»…ä¾›å¨±ä¹å’Œå‚è€ƒã€‚
          </p>
          <p className="sdisclaimer">
          Â©2025 MingLiç†å‘½
          </p>
        </div>
      </footer>

    </div>
    
  );
}

export default App;



